.tabs {
  margin-top: 40px;
  margin-bottom: 80px;
}
[role="tablist"] {
  margin-bottom: -1px;
}

.tablist {
  gap: 0;
}


[role="tab"] {
  flex: 1;
  z-index: 1;
  border-radius: 0;
  border: 0;
  padding: 8px 0;
  text-align: left;
  background-color: transparent;
  color:  var(--color-foreground);
  font-family: var(--font-heading-family);
  font-size: 112.5%;
  cursor: pointer;
}


[role="tab"][aria-selected="true"] {
  z-index: 3;
  position: relative;
  &:after {
    content: " ";
    height: 5px;
    background: var(--color-foreground);
    position: absolute;
    left: 0;
    top: 100%;
    right: 0;
    display: block;
  }
}


[role="tabpanel"] {
  position: relative;
  padding: 24px 0;
  border-top: 1px solid var(--color-foreground);
  //background: white;
  z-index: 2;
  .tab_flex {
    width: 100%;
    align-items: center;
    justify-content: flex-start;
    .text {
      flex: 1 1 100%;
      padding: 40px;
      @media screen and (min-width: 1024px) {
        padding: 56px;
        max-width: 885px;
      }
      > ul.tabs {
        display: none;
        visibility: hidden;
      }
    }
    &.withimage {
      .text, .image {
        @media screen and (min-width: 960px) {
          flex: 1 1 45%;
          justify-content: stretch;
        }
        img {
          max-width: 100%;
        }
      }
      .text {
        padding: 8px;
        @media screen and (min-width: 1024px) {
          padding: 56px 56px 56px 0;
          max-width: 100%;
        }
      }
    }
  }
}


[role="tabpanel"]:focus {
  border-color: var(--color-foreground);
  outline: 1px solid var(--color-foreground);
}
