.paid {
  .paid {
    display: block;
    visibility: visible;
  }
  .free {
    display: none;
    visibility: hidden;
  }
}
.free {
  .free {
    display: block;
    visibility: visible;
  }
  .paid {
    display: none;
    visibility: hidden;
  }
}

.grid.product_page {
  grid-template-columns: 1fr;
  @media screen and (min-width: 768px) {
    grid-template-columns: repeat(12, minmax(0, 1fr));
    gap: 24px;
    margin-top: 28px;
  }
  .prod_image_container {
    .mobile_slideNav {
      display: none;
      visibility: hidden;
      justify-content: space-around;
    }
    @media screen and (max-width: 767px) {
      overflow: hidden;
      margin: 0 -8px;
      .mobile_slideNav {
        display: flex;
        visibility: visible;
        gap: 0;
        justify-content: center;
        button.dotty {
          width: 40px;
          padding: 16px;
          margin: 0;
          flex: 0 1 40px;
          span {
            display: block;
            background-color: var(--color-shade);
            border-radius: 50%;
            height: 8px;
            width: 8px;
            transition: var(--transition-fast);
          }
          &.intersecting {
            span {
              background-color: var(--color-dark);
            }
          }
        }
      }
      .product-image.grid {
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        flex-wrap: nowrap;
        scroll-snap-type: x mandatory;	
        -webkit-overflow-scrolling: touch;
        overflow-x: scroll;
        scroll-behavior: smooth;
        -ms-overflow-style: none;  /* IE and Edge */
        scrollbar-width: none;  /* Firefox */
        &::-webkit-scrollbar {
          display: none;
        }
        .square, .landscape {
          flex: 1 0 100vw;
          width: calc(100vw - 16px);
          min-width: calc(100vw - 16px);
          scroll-snap-align: start;
          position: relative;
        }
      }
    }
    @media screen and (min-width: 768px) {
     grid-column: 1 / span 7;
     .product-image.grid {
       grid-template-columns: repeat(1, minmax(0, 1fr));
       gap: 8px;
     }
    }
    @media screen and (min-width: 1260px) {
      .product-image.grid {
         grid-template-columns: repeat(2, minmax(0, 1fr));
         gap: 16px;
     }
   }
   img {
      display: block;
    }
    .square {
      grid-column: span 1;
    }
    .landscape {
      grid-column: span 1;
      @media screen and (min-width: 1260px) {
        grid-column: span 2;
      }
    }
  }
  .product_info {
    h1 {
      font-size: 250%;
      margin-bottom: 16px;
      @media screen and (min-width: 1024px) {
        margin-bottom: 16px;
      }
    }
   @media screen and (min-width: 768px) {
     grid-column: 8 / span 5; 
   }
  }
}

ul.features {
  padding: 0;
  margin: 0;
  gap: 10px;
  justify-content: flex-start;
  li {
    flex: 1 0 100%;
    @media screen and (min-width: 420px) {
      flex: 1 1 48%;
    }
    @media screen and (min-width: 768px) {
      flex: 1 1 100%;
    }
    @media screen and (min-width: 1200px) {
      flex: 1 1 35%;
    }
    font-size: 93.75%;
    gap: 8px;
    justify-content: flex-start;
    //align-items: center;
    svg {
      margin: 1px 0 0 16px;
    }
    &:last-child {
      margin-bottom: 24px;
    }
  }
}

.breadcrumbs {
  ol {
    text-align: left;
    list-style-type: none;
    padding: 0;
    margin-top: 0;
    li {
      display: inline-block;
      margin-right: 12px;
      a {
        font-size: 87.5%;
        font-family: var(--font-heading-family);
        color: rgba(var(--color-red-rgb),0.7);
        position: relative;
        padding-right: 8px;
        &:after {
          position: absolute;
          left: calc(100% - 1px);
          content: ">";
          display: block;
          bottom: 0;
        }
        &[aria-current="page"] {
          color: var(--color-red);
          &:after {
            content: "";
          }
        }
      }
    }
  }
}

@media screen and (min-width: 768px) {
  .product__info-container--sticky {
    position: sticky;
    top: 64px;
    z-index: 2;
    max-width: 666px;
    margin-left: auto;
    margin-right: auto;
  }

  .product__media-container .slider-buttons {
    display: none;
  }
}


/* Dynamic checkout */

.shopify-payment-button__button {
  border-radius: 0;
  font-family: inherit;
  min-height: 4.6rem;
}

.shopify-payment-button__button [role="button"].focused,
.no-js .shopify-payment-button__button [role="button"]:focus {
  outline: .2rem solid rgba(var(--color-foreground),.5) !important;
  outline-offset: 0.3rem;
  box-shadow: 0 0 0 .1rem rgba(var(--color-button),var(--alpha-button-border)),0 0 0 .3rem rgb(var(--color-background)),0 0 .5rem .4rem rgba(var(--color-foreground),.3) !important;
}

.shopify-payment-button__button [role="button"]:focus:not(:focus-visible) {
  outline: 0;
  box-shadow: none !important;
}

.shopify-payment-button__button [role="button"]:focus-visible {
  outline: .2rem solid rgba(var(--color-foreground),.5) !important;
  box-shadow: 0 0 0 .1rem rgba(var(--color-button),var(--alpha-button-border)),0 0 0 .3rem rgb(var(--color-background)),0 0 .5rem .4rem rgba(var(--color-foreground),.3) !important;
}

.shopify-payment-button__button--unbranded {
  background-color: rgba(var(--color-button), var(--alpha-button-background));
  box-shadow: 0 0 0 0.1rem rgba(var(--color-button), var(--alpha-button-border));
  color: rgb(var(--color-button-text));
  font-size: 1.4rem;
  line-height: 1.2;
  letter-spacing: 0.07rem;
}

.shopify-payment-button__button--unbranded::selection {
  background-color: rgba(var(--color-button-text), 0.3);
}

.shopify-payment-button__button--unbranded:hover,
.shopify-payment-button__button--unbranded:hover:not([disabled]) {
  background-color: rgba(var(--color-button), var(--alpha-button-background));
  box-shadow: 0 0 0 0.2rem rgba(var(--color-button), var(--alpha-button-border));
}

.shopify-payment-button__more-options {
  margin: 1.6rem 0 1rem;
  font-size: 1.2rem;
  line-height: 1.5;
  letter-spacing: 0.05rem;
  text-decoration: underline;
  text-underline-offset: 0.3rem;
}

.shopify-payment-button__button--hidden {
  display: none;
}

/* Product form */

.product-form {
  display: block;
}

.product-form__error-message-wrapper:not([hidden]) {
  display: flex;
  align-items: flex-start;
  font-size: 1.2rem;
  margin-bottom: 1.5rem;
}

.product-form__error-message-wrapper svg {
  flex-shrink: 0;
  width: 1.2rem;
  height: 1.2rem;
  margin-right: 0.7rem;
  margin-top: 0.5rem;
}

/* Form Elements */
.product-form__input {
  flex: 0 0 100%;
  padding: 0;
  margin: 0 0 1.2rem 0;
  min-width: fit-content;
  border: none;
}

variant-radios,
variant-selects {
  display: block;
}

.product-form__input--dropdown {
  margin-bottom: 1.6rem;
}

.product-form__input .form__label {
  padding-left: 0;
}

fieldset.product-form__input .form__label {
  margin-bottom: 0.2rem;
}

.product-form__input input[type='radio'] {
  clip: rect(0, 0, 0, 0);
  overflow: hidden;
  position: absolute;
  height: 1px;
  width: 1px;
}

.product-form__input input[type='radio'] + label {
  border: 1px solid var(--color-foreground);
  color: rgb(var(--color-foreground));
  display: inline-block;
  padding: 1rem 2rem;
  line-height: 1;
  text-align: center;
  transition: all var(--duration-short) ease;
  cursor: pointer;
}

.product-form__input input[type='radio'] + label:hover {
  background-color: rgba(var(--color-red-rgb),0.5);
}

.product-form__input input[type='radio']:checked + label {
  background-color: rgba(var(--color-red-rgb),1);
  color: white;
}

@media screen and (forced-colors: active) {
  .product-form__input input[type=radio]:checked + label {
    text-decoration: underline;
  }
}

.product-form__input input[type='radio']:checked + label::selection {
  background-color: rgba(var(--color-red-rgb), 0.3);
}

.product-form__input input[type='radio']:disabled + label, .product-form__input input[type='radio'].disabled + label {
  border-color: rgba(var(--color-red-rgb), 0.1);
  color: rgba(var(--color-red-rgb), 0.4);
  text-decoration: line-through;
}

.product-form__input input[type='radio'].disabled:checked + label, .product-form__input input[type='radio']:disabled:checked + label {
  color: rgba(var(--color-background),.4);
}

.product-form__input input[type='radio']:focus-visible + label {
  box-shadow: 0 0 0 0.3rem rgb(var(--color-background)),
    0 0 0 0.5rem rgba(var(--color-red-rgb), 0.55);
}

/* Fallback */
.product-form__input input[type='radio'].focused + label,
.no-js .shopify-payment-button__button [role="button"]:focus + label {
  box-shadow: 0 0 0 0.3rem var(--color-background),
    0 0 0 0.5rem rgba(var(--color-red-rgb), 0.55);
}

/* No outline when focus-visible is available in the browser */
.no-js .product-form__input input[type='radio']:focus:not(:focus-visible) + label {
  box-shadow: none;
}

.product-form__input .select {
  max-width: 25rem;
}

.product-form__submit {
  margin-bottom: 1rem;
}

.no-js .product-form__submit.button--secondary {
  --color-button: var(--color-base-accent-1);
  --color-button-text: var(--color-base-solid-button-labels);
  --alpha-button-background: 1;
}

.product-form__submit[aria-disabled="true"] + .shopify-payment-button,
.product-form__submit[disabled] + .shopify-payment-button {
  display: none;
}

@media screen and (forced-colors: active) {
  .product-form__submit[aria-disabled="true"] {
    color: Window;
  }
}

/* Overrides */
.shopify-payment-button__more-options {
  color: rgb(var(--color-foreground));
}

.shopify-payment-button__button {
  font-size: 1.5rem;
  letter-spacing: 0.1rem;
}

.product_description {
  margin: 0 0 24px 0;
  &.rte, &.rte p {
    font-size: 93.75%;
    line-height: 1.6;
  }
}

.buy {
  margin: 0;
  .product-form__buttons, button {
    margin-bottom: 0;
