.grid.product_page {
  grid-template-columns: 1fr;
}
@media screen and (min-width: 768px) {
  .grid.product_page {
    grid-template-columns: repeat(12, minmax(0, 1fr));
    gap: 24px;
    margin-top: 28px;
  }
}
.grid.product_page .prod_image_container .mobile_slideNav {
  display: none;
  visibility: hidden;
  justify-content: space-around;
}
@media screen and (max-width: 767px) {
  .grid.product_page .prod_image_container {
    overflow: hidden;
    margin: 0 -8px;
  }
  .grid.product_page .prod_image_container .mobile_slideNav {
    display: flex;
    visibility: visible;
    gap: 0;
    justify-content: center;
  }
  .grid.product_page .prod_image_container .mobile_slideNav button.dotty {
    width: 40px;
    padding: 16px;
    margin: 0;
    flex: 0 1 40px;
  }
  .grid.product_page .prod_image_container .mobile_slideNav button.dotty span {
    display: block;
    background-color: var(--color-shade);
    border-radius: 50%;
    height: 8px;
    width: 8px;
    transition: var(--transition-fast);
  }
  .grid.product_page .prod_image_container .mobile_slideNav button.dotty.intersecting span {
    background-color: var(--color-dark);
  }
  .grid.product_page .prod_image_container .product-image.grid {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    flex-wrap: nowrap;
    scroll-snap-type: x mandatory;
    -webkit-overflow-scrolling: touch;
    overflow-x: scroll;
    scroll-behavior: smooth;
    -ms-overflow-style: none;
    /* IE and Edge */
    scrollbar-width: none;
    /* Firefox */
  }
  .grid.product_page .prod_image_container .product-image.grid::-webkit-scrollbar {
    display: none;
  }
  .grid.product_page .prod_image_container .product-image.grid .square, .grid.product_page .prod_image_container .product-image.grid .landscape {
    flex: 1 0 100vw;
    width: calc(100vw - 16px);
    min-width: calc(100vw - 16px);
    scroll-snap-align: start;
    position: relative;
  }
}
@media screen and (min-width: 768px) {
  .grid.product_page .prod_image_container {
    grid-column: 1/span 7;
  }
  .grid.product_page .prod_image_container .product-image.grid {
    grid-template-columns: repeat(1, minmax(0, 1fr));
    gap: 8px;
  }
}
@media screen and (min-width: 1260px) {
